<template>
    <div>
         
        <van-nav-bar
            title="注册用户" 
            left-text="返回" 
            left-arrow 
            @click-left="goback" 
        /> <div class="register-panel">
            <van-field 
                v-model="username" 
                label="用户名" 
                icon="clear" 
                placeholder="请输入用户名" 
                required 
                @click-icon="username = ''"
                :error-message="usernameErrMsg"
            ></van-field>
            <van-field 
                v-model="password" 
                type="password" 
                label="密码" 
                placeholder="请输入密码" 
                required
                :error-message="passwordErrMsg"
            ></van-field>
            <div class="register-buttom">
                <van-button type="primary" @click="registerAction" size="large" :loading="openLoading">马上注册</van-button>
            </div>
        </div>
       
    </div>
</template>

<script>
    import axios from 'axios';
    import url from '@/serviceAPI.config.js';
    import {Toast} from 'vant';
    export default {
        data(){
            return {
                username:'',
                password:'',
                openLoading:false,//是否开启用户注册的loading状态
                usernameErrMsg:'',  //当用户注册用户名出错时的提示
                passwordErrMsg:'',  //当用户注册密码出错时的提示

            }
        },
        methods:{
            //返回上一页
            goback(){
                this.$router.go(-1)
            },

            registerAction(){
                // if (this.checkForm()) {
                //     this.axiosRegisterUser()
                // }
                this.checkForm() && this.axiosRegisterUser()
            },

            // axios注册用户方法
            axiosRegisterUser(){
                this.openLoading = true;
                axios({
                    url:url.registerUser,
                    method:'post',
                    data:{
                        userName:this.username,
                        password:this.password
                    }
                })
                .then(response=>{
                    console.log(response);
                    if (response.data.code==200) {
                        Toast.success(response.data.message);
                        this.$router.push('/member'); //跳转到首页
                    }else{
                        console.log(response.data.message);
                        Toast.fail('注册失败！');
                        this.openLoading=false;
                    }
                })
                .catch((error)=>{
                    console.log(error);
                    Toast.fail('注册失败！！！');
                    this.openLoading=false;
                })
            },

            //验证用户名、密码表单方法
            checkForm(){
                let isOK = true;
                if (this.username.length<5) {
                    this.usernameErrMsg = "用户名不能少于5位！"
                    isOK = false;
                }else{
                    this.usernameErrMsg = ""
                };
                if (this.password.length<6) {
                    this.passwordErrMsg = "密码不能少于6位！"
                    isOK = false;
                }else{
                    this.passwordErrMsg = ""
                };

                return isOK
            },
        }
    }
</script>

<style scoped>
.register-panel{
        width:90%;
        border-radius: 5px;
        margin:20px auto;
        padding-bottom:50px;
    }
    .register-buttom{
        padding-top:20px;
    }
</style>


