<template>
    <div>
         
        <van-nav-bar
            title="用户登录" 
            left-text="返回" 
            left-arrow 
            @click-left="goback" 
        /> <div class="register-panel">
            <van-field 
                v-model="username" 
                label="用户名" 
                icon="clear" 
                placeholder="请输入用户名" 
                required 
                @click-icon="username = ''"
                :error-message="usernameErrMsg"
            ></van-field>
            <van-field 
                v-model="password" 
                type="password" 
                label="密码" 
                placeholder="请输入密码" 
                required
                :error-message="passwordErrMsg"
            ></van-field>
            <div class="register-buttom">
                <van-button type="primary" @click="loginAction" size="large" :loading="openLoading">登录</van-button>
            </div>
        </div>
       
    </div>
</template>

<script>
    import axios from 'axios';
    import url from '@/serviceAPI.config.js';
    import {Toast} from 'vant';
    export default {
        data(){
            return {
                username:'',
                password:'',
                openLoading:false,//是否开启用户注册的loading状态
                usernameErrMsg:'',  //当用户注册用户名出错时的提示
                passwordErrMsg:'',  //当用户注册密码出错时的提示

            }
        },
        created(){
            //进入页面时，读取本地存储，判断是否可以登录
            if (localStorage.userInfo) {
                Toast.success('您已成功登录')
                this.$router.push('/main')
            }
        },
        methods:{
            //返回上一页
            goback(){
                this.$router.go(-1)
            },

            loginAction(){
                // if (this.checkForm()) {
                //     this.axiosRegisterUser()
                // }
                this.checkForm() && this.axiosLoginUser()
            },

            // axios登录方法
            axiosLoginUser(){
                this.openLoading = true;
                axios({
                    url:url.login,
                    method:'post',
                    data:{
                        userName:this.username,
                        password:this.password
                    }
                })
                .then(response=>{
                    // console.log(response);
                    if (response.data.code == 200 && response.data.message == true) {
                       //本地存储，存储下用户名和密码
                        new Promise((resolve,reject)=>{
                            localStorage.userInfo = {userName:this.username,password:this.password}
                            setTimeout(()=>{resolve()},500)
                        }).then(()=>{
                            Toast.success('登录成功！');
                            this.$router.push('/main');
                        }).catch(err=>{
                            Toast.fail('登录状态保存失败！')
                            console.log(err);
                        })
                   }else{
                       Toast.fail('登录失败！');
                       this.openLoading = false;
                   }
                })
                .catch((error)=>{
                    Toast.fail('登录失败！');
                    this.openLoading = false
                })
            },

            //验证用户名、密码表单方法
            checkForm(){
                let isOK = true;
                if (this.username.length<5) {
                    this.usernameErrMsg = "用户名不能少于5位！"
                    isOK = false;
                }else{
                    this.usernameErrMsg = ""
                };
                if (this.password.length<6) {
                    this.passwordErrMsg = "密码不能少于6位！"
                    isOK = false;
                }else{
                    this.passwordErrMsg = ""
                };
                return isOK
            },
        }
    }
</script>

<style scoped>
.register-panel{
        width:90%;
        border-radius: 5px;
        margin:20px auto;
        padding-bottom:50px;
    }
    .register-buttom{
        padding-top:20px;
    }
</style>


