<template>
    <div>
        <div class="narbar-div">
            <van-nav-bar 
                title="类别列表"
                
            />
        </div>
        
        <div>
            <van-row>
                <van-col span="6">
                    <div id="left-nav">
                        <ul>
                            <li @click="clickCategory(index,item.ID)" :class="{categoryActive:categoryIndex==index}" v-for="(item,index) in category" :key="index">
                                {{item.MALL_CATEGORY_NAME}}
                            </li>
                        </ul>
                        <!-- <van-tree-select
                            :items="category"
                            :main-active-index="categoryIndex"
                            :active-id = "active"
                            @click="clickCategory"
                        /> -->
                    </div>
                </van-col>
                <van-col span="18">
                    <div class="tabCategorySub">
                        <van-tabs v-model="active" @click="onClickCategorySub">
                            <van-tab v-for="(item,index) in categorySub" :key="index" :title="item.MALL_SUB_NAME" ></van-tab>
                        </van-tabs>
                    </div>
                    <div id="list-div">
                        <van-pull-refresh v-model="isRefresh" @refresh="onRefresh">
                            <van-list v-model="loading" :finished="finished" @load="onLoad" finished-text="没有更多了">
                                <div class="list-item" v-for="(item,index) in goodList" :key="index" @click="goGoodsInfo(item.ID)">
                                    <div class="list-item-img">
                                        <img :onError="errorImg" :src="item.IMAGE1" width="100%" alt="">
                                    </div>
                                    <div class="list-item-text">
                                        <div>{{item.NAME}}</div>
                                        <div>售价：￥{{item.ORI_PRICE | moneyFilter}}元</div>
                                    </div>
                                </div>
                            </van-list>
                        </van-pull-refresh>
                    </div>
                </van-col>
            </van-row>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import url from '@/serviceAPI.config.js'
    import { toMoney } from '@/filter/moneyFilter.js';
    import { Toast } from 'vant';
    export default {
        filters: {
            moneyFilter(money) {
                return toMoney(money)
            }
        },
        data() {
            return {
                category: [],
                categoryIndex:0,
                categorySub:[],//小类类别
                active:0, //激活标签的值
                loading:false,
                finished:false,//上拉加载是否有数据
                // list:[], //商品假数据
                page:1,     //商品列表页数
                goodList:[],    //商品列表数据
                categorySubId:'',   //商品子类id
                isRefresh:false,//下拉刷新
                errorImg:'this.src="'+require('@/assets/images/errorimg.png')+'"',  //错误图片
            }
        },
        created () {
            this.getCategory();
            // this.getCategorySubByCategoryId(1);
        },
        mounted(){
            let winHeight = document.documentElement.clientHeight
            document.getElementById("left-nav").style.height= winHeight-46-50 +'px'
            document.getElementById("list-div").style.height= winHeight-90-50 +'px'
        },
        methods: { 
            //获取大类列表信息
            getCategory() {
                axios({
                    url:url.getCategoryList,
                    method:'get',
                })
               .then(response=>{
                //    console.log(response)
                    if(response.data.code == 200 && response.data.message ){
                        this.category = response.data.message
                        
                        this.getCategorySubByCategoryID(this.category[0].ID)
                        
                    }else{
                       Toast('服务器错误，数据获取失败！')
                    }
               })
               .catch(error=>{
                   console.log(error);
                   
               })
            },

            //点击左侧大类时的逻辑
            clickCategory(index,categoryId){
                this.categoryIndex=index
                this.page=1
                this.finished = false
                this.goodList= [] 
                this.getCategorySubByCategoryID(categoryId)
            },

            //根据大类id获取小类类别列表信息
            getCategorySubByCategoryID(categoryId){
                axios({
                    url:url.getCategorySubList,
                    method:'post',
                    data:{categoryId:categoryId}
                })
                .then(response=>{

                    if (response.data.code == 200 && response.data.message) {
                        // console.log(response.data.message);
                        this.active = 0;
                        this.categorySub=response.data.message;
                        this.categorySubId = this.categorySub[0].ID;
                        this.onLoad();
                    }
                })
                .catch(error=>{
                   console.log(error);
                   
               })
            },

            //上拉加载方法
            onLoad(){
                setTimeout(()=>{
                    this.categorySubId = this.categorySubId
                    // this.categorySubId = this.categorySubId?this.categorySubId:this.categorySub[0].ID
                    this.getGoodList()
                },1000)
            },

            //下拉刷新方法
            onRefresh(){
                setTimeout(() => {
                    this.isRefresh=false;
                    this.finished = false;
                    this.goodList=[]
                    this.page=1
                    this.onLoad()
                }, 500);
            },

            //获取商品数据列表
            getGoodList(){
                axios({
                    url:url.getGoodsListByCategorySubID,
                    method:'post',
                    data:{
                        categorySubId:this.categorySubId,
                        page:this.page
                    }
                })
                .then(response=>{
                    // console.log(response);
                    if (response.data.code==200 && response.data.message.length) {
                        this.page++;
                        this.goodList = this.goodList.concat(response.data.message);    //concat() 方法用于连接两个或多个数组
                    }else{
                        this.finished = true;
                    }
                    this.loading = false;
                    
                })
                .catch(error=>{
                    console.log(error);
                    
                })
            },

            //点击子类的方法
            onClickCategorySub(index,title){
                this.categorySubId = this.categorySub[index].ID;
                // console.log('categorySubId:'+this.categorySubId);
                this.goodList=[];
                this.finished = false;
                this.page = 1;
                this.onLoad();
            },

            //跳转到商品详情页
            goGoodsInfo(id){
                //params传参，路径不能使用path，只能使用name
                //query传参，用的是path，而不是name,否则也会出错。
                this.$router.push({name:'Goods',params:{goodsId:id}})
            }
        },
    }
</script>

<style scoped>
    #left-nav{
        background-color:aliceblue;
    }
    #left-nav ul li{
        line-height: 2.4rem;
        border-bottom: 1px solid #e4e7ed;
        padding: 3px;
        font-size: 0.8rem;
        text-align: center;
    }
    .categoryActive{
        background-color: #fff;
    }

    .list-item{
        display: flex;
        flex-direction: row;
        font-size:0.8rem;
        border-bottom: 1px solid #f0f0f0;
        background-color: #fff;
        padding:5px;
    }
    #list-div{
        overflow: scroll;
    }

    .list-item-img{
        flex:8;
    }
    .list-item-text{
        flex:16;
        margin-top:10px;
        margin-left:10px;
    }
</style>


