<template>
    <div>
        <div class="nav-bar">
            <van-nav-bar   title="购物车" /> 
        </div>
        <div class="cart-title">
            <van-button size="small" type="danger" @click="clearCart" plain>清空购物车</van-button>
        </div>
        <!-- 显示购物车商品 -->
        <div class="cart-list">
            <div class="cart-row" v-for="(item,index) in cartInfo" :key="index">
                <div class="cart-image"><img :src="item.image" width="100%" alt=""></div>
                <div class="cart-text">
                    <div class="cart-goodsName">{{item.name}}</div>
                    <div class="cart-control">
                        <van-stepper v-model="item.count" />
                    </div>
                </div>
                <div class="cart-price">
                    <div>
                        ￥{{item.price | moneyFilter}}
                    </div>
                    <div>
                        数量：{{item.count}}
                    </div>
                    <div class="allPrice">
                        ￥{{item.price*item.count | moneyFilter }}
                    </div>
                </div>
            </div>
        </div>
        <!-- 总价 -->
        <div class="totalMoney">
            商品总价：￥{{totalMoney | moneyFilter}}
        </div>
    </div>
</template>

<script>
    import { toMoney } from '@/filter/moneyFilter.js';
    export default {
        data() {
            return {
                cartInfo: [], //购物车信息
                isEmpty:false, //购物车是否为空
            }
        },
        methods: {
            //获取购物车数据分方法
            getCartInfo() {
                
                if (localStorage.cartInfo) {
                    this.cartInfo = JSON.parse(localStorage.cartInfo)
                }
                // console.log(JSON.stringify(this.cartInfo));
                
                this.isEmpty = this.cartInfo.length > 0 ? true : false

            },

            //清空购物车
            clearCart(){
                localStorage.removeItem('cartInfo');
                this.cartInfo = [];
            },
        },
        created () {
            this.getCartInfo();
        },
        filters: {
            moneyFilter(money) {
                return toMoney(money)
            }
        },
        computed: {
            totalMoney() {
                let allMoney = 0;
                this.cartInfo.forEach((item,index)=>{
                    allMoney += item.price*item.count
                })
                localStorage.cartInfo = JSON.stringify(this.cartInfo)
                return allMoney
            }
        },
    }
</script>

<style scoped>
    .cart-title{
        height: 2rem;
        line-height: 2rem;
        background-color: #fff;
        border-bottom: qpx solid #E4E7ED;
        padding:5px;
        text-align:  right;
    }
    .cart-list{
        background-color: #fff;
    }
    .cart-row{
        display: flex;
        flex-direction: row;
        flex-wrap:nowrap;
        padding: 0.5rem;
        font-size: 0.8rem;
        border-bottom: 1px solid #E4E7ED;
    }
    .cart-image{
        flex: 6;
    }
    .cart-text{
        flex: 14;
        padding-left:10px; 
    }
    .cart-control{
        padding-top: 10px;
    }
    .cart-price{
        flex: 4;
        text-align: right;
    }
    .allPrice{
        color: red;
    }
    .totalMoney{
        text-align: right;
        background-color: #fff;
        border-bottom: 1px solid #E4E7ED;
        padding: 5px;
    }
</style>


