<template>
  <div>
    <div class="search-bar">
      <van-row class="test-row">
        <van-col span="3">
          <img :src="locationIcon" width="70%" alt class="locationicon">
        </van-col>
        <van-col span="16">
          <input type="text" class="search-input">
        </van-col>
        <van-col span="5">
          <van-button type="default" size="mini" class="search-btn">搜索</van-button>
        </van-col>
      </van-row>
    </div>
     <!-- swiper 区域 -->
    <div class="swiper-area">
      <van-swipe :autoplay="2000">
        <van-swipe-item v-for="(banner,index) in bannerPicArray" :key="index">
          <img v-lazy="banner.image" width="100%" alt>
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- 导航条 -->
    <div class="type-bar">
      <div v-for="(item, index) in category" :key="index">
        <img v-lazy="item.image" alt width="90%">
        <span>{{item.mallCategoryName}}</span>
      </div>
    </div>
    <!-- 广告 -->
    <div>
      <img v-lazy="this.adBanner" width="100%">
    </div>
    <!-- 商品推荐 -->
    <div class="recommend-area">
      <div class="recommend-title">商品推荐</div>
      <div class="recommend-body">
        <swiper :options="swiperOption">
          <swiper-slide v-for="(item,index) in recommendGoods" :key="index">
            <div class="recommend-item">
              <img v-lazy="item.image" width="80%" alt>
              <div>{{item.goodsName}}</div>
              <div>原价:{{item.price | moneyFilter}}元(特价:{{item.mallPrice| moneyFilter}}元)</div>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
    <!-- swiper -->
    <!-- <swiperDefault></swiperDefault>
    <swiperText></swiperText> -->
    <!-- 楼层部分 -->
    <floorComponent :floorData="floor1" :floorTitle="floorName.floor1"></floorComponent>
    <floorComponent :floorData="floor2" :floorTitle="floorName.floor2"></floorComponent>
    <floorComponent :floorData="floor3" :floorTitle="floorName.floor3"></floorComponent>
    <!-- 热卖部分 -->
    <div class="hot-area">
      <div class="hot-title">热卖商品</div>
      <div class="hot-goods">
        <!-- list组件 -->
        <van-list>
            <van-row gutter="0">
                <van-col span="12" v-for="(item,index) in hotGoods" :key="index">
                    <goods-info :goodsId="item.goodsId" :goodsImage="item.image" :goodsName="item.name" :goodsPrice="item.price"></goods-info>
                </van-col>
            </van-row>
        </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

// import swiperDefault from "../swiper/swiperDefault2";
// import swiperText from "../swiper/swiperText";

import floorComponent from "../component/floorComponent";

import { toMoney } from '@/filter/moneyFilter.js';

import goodsInfo from '../component/goodsInfoComponent';

import url from '@/serviceAPI.config.js';
export default {
  data() {
    return {
      msg: "李欣shoppingmall",
      locationIcon: require("../../assets/images/location.png"),
      bannerPicArray: [],
      category: [],
      adBanner: "",
      recommendGoods: [],
      swiperOption: {
        slidesPerView: 3,
        slidesPerGroup: 1,
        autoplay: true
      },
      floor1: [],
      floor2: [],
      floor3: [],
      floorName: "",
      hotGoods:[],//热卖商品
    };
  },
  components: { 
    swiper, 
    swiperSlide, 
    // swiperDefault, 
    // swiperText,
    floorComponent, 
    goodsInfo
  },
  created() {
   axios({
      url:url.getShoppingMallInfo,
      method: "get"
    })
      .then(response => {
        // console.log(response);
        if (response.status == 200) {
          this.category = response.data.data.category;
          this.adBanner = response.data.data.advertesPicture.PICTURE_ADDRESS;
          this.bannerPicArray = response.data.data.slides;
          this.recommendGoods = response.data.data.recommend;
          this.floor1 = response.data.data.floor1;
          this.floor2 = response.data.data.floor2;
          this.floor3 = response.data.data.floor3;
          this.floorName = response.data.data.floorName;
          this.hotGoods = response.data.data.hotGoods;           //热卖商品
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  filters: {
    moneyFilter(money) {
      return toMoney(money)
    }
  }
};
</script>

<style scoped>
.search-bar {
  /* height: 2.2rem; */
  background-color: #e5017d;
  line-height: 2.2rem;
}
.search-input {
  width: 100%;
  height: 1.3rem;
  border: none;
  border-bottom: 1px solid #fff;
  background-color: #e5017d;
  color: #fff;
}
.locationicon {
  display: block;
  margin: 0.2rem auto;
}
.search-btn {
  display: block;
  margin: 0.6rem auto;
}
.swiper-area {
  max-height: 10rem;
  overflow: hidden;
  clear: both;
}
/* .van-swipe{
        height: 100%;
    }
    .van-swipe-item{
        height: 100%;
    }
    .van-swipe-item img{
        height: 100%;
    } */

.type-bar {
  background-color: #fff;
  margin: 0.3rem;
  border-radius: 0.3rem;
  font-size: 14px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.type-bar div {
  flex: 1;
  padding: 0.2rem;
  font-size: 12px;
  text-align: center;
}
.recommend-area {
  background-color: #fff;
  margin-top: 0.3rem;
}
.recommend-title {
  border-bottom: 1px solid #eee;
  font-size: 14px;
  padding: 0.4rem;
  color: #e5017d;
}
.recommend-body {
  border-bottom: 1px solid #eee;
}
.recommend-item {
  width: 99%;
  border-right: 1px solid #eee;
  font-size: 12px;
  text-align: center;
}
.hot-area {
  text-align: center;
  font-size: 14px;
  height: 1.8rem;
  line-height: 1.8rem;
}
.hot-goods{
  /* height: 150rem; */
  /* overflow: hidden; */
  padding-bottom: 5rem;
  background-color: #fff;
}
</style>


